import React from 'react';
import './style.css';
import PropTypes from 'prop-types';


export default class Archieve extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            focus: false
        }
    }
    setFocus(focus) {
        this.setState({ focus })
    }

    render() {
        const { focus } = this.state;
        return (
            <div style={{ ...this.props.style }}>
                <div className="archieve-container">
                    <h2
                        style={{
                            transition: 'all 0.8s',
                            opacity: focus ? 1 : 0,
                        }}
                    >Thành tựu</h2>
                </div>
            </div>

        )
    }

}

Archieve.propTypes = {
    setFocus: PropTypes.func
}